import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;


enum MenuType {
  FILE,
  SKEL,
  ANIMATION,
  SKIN,
}

@ccclass('Menu')
export class Menu extends Component {

  @property(Node)
  fileOperPanel: Node = null;

  @property(Node)
  animationPanel: Node = null;

  @property(Node)
  skinPanel: Node = null;



  start() {

  }



  private updateOperPanle( operType: MenuType ): void {

    this.fileOperPanel.active = (operType == MenuType.FILE)
    this.animationPanel.active = (operType == MenuType.ANIMATION)
    this.skinPanel.active = (operType == MenuType.SKIN)
  }




  onFileMenu () {

    let active = this.fileOperPanel.active;
    this.fileOperPanel.active = !active;

  }


  onSkeleron () {
    this.updateOperPanle(MenuType.SKEL);

  }

  onAnimation() {
    this.updateOperPanle(MenuType.ANIMATION);
    let active = this.animationPanel.active;
    this.animationPanel.active = !active;

  }

  onSkin(){
    this.updateOperPanle(MenuType.SKIN);
    let active = this.skinPanel.active;
    this.skinPanel.active = !active;

  }

  onToggleDebug() {}
  onToggleLoop() {}
}

